/*MyIcon*/
@keyframes animyicon {
    0%  {
        transform: scale(1.0, 1.0) translate(0, -100px);
    }
    15% {
        transform: scale(0.98, 0.9) translate(0, -95px);
    }
    30% {
        transform: scale(1.02, 1.0) translate(0, -92px);
    }
    50% {
        transform: scale(0.98, 1.05) translate(0, -108px);
    }
    70% {
        transform: scale(1.0, 0.9) translate(0, -95px);
    }
    100% {
        transform: scale(1.0, 1.0) translate(0, -100px);
    }
    0%, 100% {
        opacity: 1;
    }
}


.my-icon {
    text-align: center;
    transform: translateY(-100px);
    animation: animyicon 1s ease-in-out 1 forwards  ;
    & img {
        border-radius: 50%;
        width: 200px;
        height: 200px;
    }
}